#wrapper {
  overflow-x: hidden;
}

#sidebar-wrapper {
  min-height: 100vh;
  margin-left: -15rem;
  -webkit-transition: margin 0.25s ease-out;
  -moz-transition: margin 0.25s ease-out;
  -o-transition: margin 0.25s ease-out;
  transition: margin 0.25s ease-out;
}

#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#sidebar-wrapper .list-group {
  width: 15rem;
}

#page-content-wrapper {
  min-width: 100vw;
}

#wrapper.toggled #sidebar-wrapper {
  margin-left: 0;
}

@media (min-width: 768px) {
  #sidebar-wrapper {
    margin-left: 0;
  }

  #page-content-wrapper {
    min-width: 0;
    width: 100%;
  }

  #wrapper.toggled #sidebar-wrapper {
    margin-left: -15rem;
  }
}

/*Progress Bar*/
.progress-bar {
  width: 400px;
  height: 3em;
  background-color: #808080;
  border-radius: 1.5em;
  position: relative;
  color: white;
}
.progress-bar::before {
  content: attr(data-label);
  display: flex;
  position: absolute;
  align-items: center;
  left: 0.5em;
  top: 0.5em;
  bottom: 0.5em;
  min-width: 2rem;
  width: var(--width, 0);
  max-width: calc(100%-1em);
  background-color: #0094ff;
  border-radius: 1em;
  padding: 1em;
}
